import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { ManageStandAloneLoadingService } from "@tei/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import { firstValueFrom } from "rxjs";
import { IClubsService } from "../../../@intermediate/clubs/clubs.interface";
import { CreateClubRequest } from "../../../@intermediate/clubs/model/post-clubs.model";
import { ICoursesService } from "../../../@intermediate/courses/courses.interface";

@Component({
  selector: "tei-tee-sheet-administration-clubs-create-club",
  templateUrl: "./create-club.component.html",
  styleUrls: ["./create-club.component.scss"]
})
export class CreateClubComponent implements OnInit
{
  isLoading = false;
  createClubForm: FormGroup;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant("TSADMINISTRATION.NAVIGATION.CLUBS"),
      relativeUrl: "../"
    },
    {
      title: this.translate.instant("TSADMINISTRATION.CLUBS.CREATE_CLUB"),
      relativeUrl: ""
    }
  ];

  defaultSettings = {
    ViewClub: {
      Administrator: true,
      Staff: true,
      Member: true,
      Public: true,
      Options: null,
      Type: 2,
      Label: "View Club"
    }
  };

  constructor(
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private snackbarService: SnackBarService,
    @Inject("IClubsService") private clubsService: IClubsService,
    @Inject("ICoursesService") private coursesService: ICoursesService
  ) { }

  ngOnInit(): void
  {
    this.initializeForm();
    // Set all designation toggles checked by default (if needed)
  }

  private initializeForm(): void
  {
    this.createClubForm = this.formBuilder.group({
      clubName: ["", [Validators.required, Validators.minLength(2)]],
      clubCode: ["", [Validators.required, Validators.minLength(2)]],
      description: ["", [Validators.maxLength(500)]],
      address: ["", [Validators.required]],
      phoneNumber: ["", [Validators.pattern(/^\+?[\d\s\-\(\)]+$/)]],
      email: ["", [Validators.email]]
    });
  }


  async createClub(): Promise<void>
  {
    const request = this.createClubForm.value;
    await firstValueFrom(this.clubsService.PostClub(request));
  }

  cancel(): void
  {
    // TODO: Implement navigation back to clubs list
  }

  onSaveClub(formData: CreateClubRequest): void
  {
    this.isLoading = true;
    function isSanitizedImage(obj: unknown): obj is { changingThisBreaksApplicationSecurity: string; }
    {
      return (
        obj !== null &&
        typeof obj === "object" &&
        "changingThisBreaksApplicationSecurity" in obj &&
        typeof (obj as any).changingThisBreaksApplicationSecurity === "string"
      );
    }

    let imageValue: string = "";
    const imageObj = formData?.Image;
    if (isSanitizedImage(imageObj))
    {
      imageValue = imageObj.changingThisBreaksApplicationSecurity;
    } else if (imageObj !== null && imageObj !== undefined)
    {
      imageValue = String(imageObj);
    }
    this.clubsService.PostClub({
      ...formData,
      Image: imageValue
    }).subscribe({
      next: () =>
      {
        this.snackbarService.showSuccess(this.translate.instant("TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE"), '', null, 'left');
        this.isLoading = false;
      },
      error: () =>
      {
        this.isLoading = false;
      }
    });
  }
} 