import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyCardModule as MatCardModule } from "@angular/material/legacy-card";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { RouterModule, Routes } from "@angular/router";
import { ManageStandAloneLoadingService } from "@tei/core";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { BreadCrumbModule, LoadingModule, SnackBarModule } from "@tei/material";
import { ClubsService } from "../../../@services/clubs.service";
import { CoursesService } from "../../../@services/courses.service";
import { ManageClubModule } from "../../../components/manage-club/manage-club.module";
import { CreateClubComponent } from "./create-club.component";

const routes: Routes = [
  { path: "", component: CreateClubComponent },
];

const CORE_MODULES = [
  TeiTextTranslatePipeModule
];

const BASE_MODULES = [
  FormsModule,
  ReactiveFormsModule,
  CommonModule
];

const MATERIAL_MODULES = [
  MatCardModule,
  MatInputModule,
  MatFormFieldModule,
  MatButtonModule
];

const TEI_MATERIAL_MODULES = [
  SnackBarModule,
  LoadingModule,
  BreadCrumbModule
];

const COMPONENTS = [CreateClubComponent];

const FEATURE_MODULES = [
  ManageClubModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES,
    ...FEATURE_MODULES,
    RouterModule.forChild(routes)
  ],
  providers: [
    ManageStandAloneLoadingService,
    { provide: "IClubsService", useClass: ClubsService },
    { provide: "ICoursesService", useClass: CoursesService }
  ]
})
export class CreateClubModule { } 